* {
  box-sizing: border-box;
}

body {
  font-family: "Karla", sans-serif;
  margin: 0;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 36px;
}

.header {
  display: flex;
  align-items: center;
  height: 65px;
  background: linear-gradient(90deg, #254d75 1.18%, #405ac4 100%);
  color: white;
  padding: 20px;
  justify-content: center;
}

.header--image {
  height: 100%;
  margin-right: 6px;
}

.header--title {
  font-size: 1.25rem;
  margin-right: auto;
  
}

.header--project {
  font-size: 0.75rem;
  font-weight: 500;
}

.form {
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
  margin-bottom: 17px;
}

.form--input {
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form--button {
  grid-column: 1 / -1;
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #254d75 1.14%, #405ac4 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.form--button:hover {
  background: linear-gradient(90.41deg, #17314a 1.14%, #283879 100%);
}


.meme {
  position: relative;
  display: flex;
  justify-content: center;
}

.meme--image {
  max-width: 100%;
  border-radius: 3px;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}
.downloadButton {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.download--button {
  display: flex;
  font-family: "Karla", sans-serif;
  padding: 20px;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #254d75 1.14%, #405ac4 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.download--button:hover {
  background: linear-gradient(90.41deg, #17314a 1.14%, #283879 100%);
}

.disclaimer {
  display: flex;
  margin-top: 200px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}

.disclaimer--trollface{
  width: 35px;
  margin-left: 10px;
}